.Reasons {
  padding: 0 2rem;
  display: flex;
  grid-template: 2rem;
}
.reasons-blur-1 {
  width: 32rem;
  height: 23rem;
  top: 6rem;
  left: 0rem;
  /* border-radius: 50%;
  filter: blur(212px);
  position: fixed; */
}
.reasons-blur-2 {
  width: 32rem;
  height: 23rem;
  top: 10rem;
  right: 0rem;
  border-radius: 50%;
  filter: blur(212px);
  position: fixed;
}
.left-r {
  flex: 1 1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  grid-auto-rows: 1fr;
}
.left-r > img {
  object-fit: cover;
}
.left-r > :nth-child(1) {
  width: 12rem;
  grid-row: 1/3;
  height: 28rem;
}
.left-r > :nth-child(2) {
  width: auto;
  grid-column: 2/4;
  height: 16rem;
}
.left-r > :nth-child(3) {
  width: 14rem;
  grid-column: 2/3;
  grid-row: 2;
}
.left-r > :nth-child(4) {
  width: 10rem;
  grid-column: 3/4;
  grid-row: 2;
  height: 11.2rem;
}
.right-r {
  flex: 1 1;
  text-transform: uppercase;
  gap: 1rem;
  display: flex;
  flex-direction: column;
}
.right-r > span {
  font-weight: bold;
  color: var(--purple);
}
.right-r > div {
  font-weight: bold;
  font-size: 3rem;
  color: white;
}
.details-r {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.details-r > div {
  display: flex;
  font-size: 1rem;
  gap: 1rem;
}
.details-r > div > img {
  width: 2rem;
  height: 2rem;
}
.partners {
  display: flex;
  gap: 1rem;
}
.partners > img {
  width: 2.5rem;
}
